* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #050813;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

.header {
  background-color: #09141e;
  padding: 2rem;
}

.navigation {
  text-align: center;
  background-color: #222735;
  margin: 0;
  padding: 10px;
}

.navigation a {
  color: #3e9f95;
  text-transform: uppercase;
  padding: 10px;
  font-weight: 600;
  text-decoration: none;
}

.navigation a:hover {
  color: #c5a284;
  filter: drop-shadow(0 0 10px #f5faae);
  font-weight: 600;
  transition: all .4s ease-in-out;
  transform: translate(-20%, -20%);
}

#bioContainer {
  color: #fff;
  background-color: #1d475ba6;
  flex-wrap: wrap;
  justify-content: center;
  margin: 2rem;
  padding: 2rem;
}

#bioContainer div {
  flex: 0 50%;
}

#UserNameAndReg, #imgDiv {
  text-align: center;
}

#userNameDiv {
  color: #7eb4cf;
  font-size: 22px;
}

#imgDiv img {
  border: 6px solid #09141e;
}

#userBox {
  color: #7eb4cf;
}

#imgDiv img {
  max-width: 20rem;
}

#deleteButton {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #64212c;
  border: 5px solid #0000;
  border-radius: 100px;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
}

#deleteButton:hover {
  filter: drop-shadow(0 0 8px #e45959);
  background-color: #9c0909;
  transition: all .4s ease-in-out;
}

#forms {
  opacity: 1;
  filter: drop-shadow(10px 10px 4px #020307);
  width: 60rem;
  height: 40rem;
  background-color: #1d253b;
  background-image: url("http://lol-stats.net/uploads/FpuvHt29W2bG6KPWKQ5ybcEW63BfFf2dIWIfbwjq.jpeg");
  background-size: cover;
  border-radius: 20px;
  justify-content: center;
  margin-right: -50%;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#forms h2 {
  text-align: center;
  text-transform: uppercase;
  color: #1d475b;
  text-shadow: -1px -1px #317392, 1px -1px #317392, -1px 1px #163646, 1px 1px #163646;
  filter: drop-shadow(0 0 8px #fde0b3);
  margin-top: 40px;
  margin-bottom: 20px;
  font-weight: bold;
}

#forms h4 {
  text-align: center;
  color: #161a25;
  background: radial-gradient(circle, #1d475b 0%, #fdd5d600 100%);
  margin-top: 20px;
  font-weight: bold;
}

#loginForm, #registerForm {
  width: 50%;
  opacity: .5;
  padding: 10px;
}

#loginForm:hover, #registerForm:hover {
  opacity: 1;
  transition: all .4s ease-in-out;
}

.twoForms input[type="text"], .twoForms input[type="password"], .twoForms select {
  width: 100%;
  box-sizing: border-box;
  border: 3px solid #fff;
  border-radius: 10px;
  margin: 8px 0;
  padding: 12px 20px;
}

.twoForms input[type="text"]:focus, .twoForms input[type="password"]:focus, .twoForms select:focus {
  border: 3px solid #f3ce95;
  outline: none;
}

.twoForms input {
  width: 100%;
}

.twoForms button {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #1d475b;
  border: 5px solid #0000;
  border-radius: 100px;
  padding: 15px 32px;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
}

.twoForms button:hover {
  filter: drop-shadow(0 0 8px #fde0b3);
  background-color: #f3ce95;
  transition: all .4s ease-in-out;
  transform: translate(-20%, -20%);
}

#loginButton {
  margin-right: -50%;
  position: absolute;
  top: 75%;
  left: 20%;
  transform: translate(-20%, -20%);
}

#signupButton {
  margin-right: -50%;
  position: absolute;
  top: 75%;
  left: 70%;
  transform: translate(-20%, -20%);
}

.yourPlace {
  color: #7eb4cf;
  text-align: center;
  margin: 20px 0;
}

.addMessageToForum {
  text-align: center;
  margin-top: 50px;
}

#sendMessageToForum {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #214f64;
  border: 5px solid #0000;
  border-radius: 20px;
  margin-bottom: 10px;
  margin-left: 10px;
  padding: 10px;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
}

#postWrapper {
  text-align: left;
  background-color: #1d475ba6;
  margin: 0 2rem;
}

.forumPost {
  border-bottom: 2px solid #161a25;
  padding: 20px;
}

.forumPost:hover {
  width: 98%;
  background-color: #2a6f8fa6;
  padding: 20px;
  transition: all .4s ease-in-out;
}

.postName {
  color: #fff;
}

.postMessage {
  color: #7eb4cf;
}

.button {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #214f64;
  border: 5px solid #0000;
  border-radius: 100px;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
  position: absolute;
  right: 50px;
}

.button:hover, #sendMessageToForum:hover {
  filter: drop-shadow(0 0 8px #fde0b3);
  background-color: #f3ce95;
  transition: all .4s ease-in-out;
}

#anyUserContainer, #showUserBio {
  color: #fff;
  text-align: center;
  background-color: #0d1c2b;
  border: 2px solid #09141e;
}

#anyUserContainer {
  margin-top: 50px;
}

#anyUserContainer button {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #214f64;
  border: 2px solid #0b1a22;
  margin: 5px 2px;
  padding: 5px;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
}

#anyUserContainer button:hover {
  filter: drop-shadow(0 0 8px #3c7d9b);
  background-color: #3c7d9b;
  border: 2px solid #0000;
  transition: all .4s ease-in-out;
}

#showUserBio img {
  width: 20%;
  border: 4px solid #09141e;
}

